import { useDrag } from "react-dnd";

interface ToyInfo {
  toy: {
    num: string;
    name: string;
    count: string;
    year: string;
    shape: string;
    color: string;
    size: string;
    favorite: boolean;
  };
}

const Toy = (props: ToyInfo) => {
  const [{ isDragging }, drag] = useDrag(() => ({
    type: "img",
    toy: { id: props.toy.num },
    collect: (monitor) => ({
      isDragging: !!monitor.isDragging(),
    }),
  }));
  return (
    <div className="tree-toy">
      <img src={`/toys/${props.toy.num}.png`} ref={drag} className="toy-item" />
      <div className="toys-count">{props.toy.count}</div>
    </div>
  );
};

export default Toy;
